<template lang="pug">
  .slider
    .slide(v-for="(project, index) in projects", :class="[project.id, 'fade-in-3']", v-show="activeSlide === index")
      .art
      .logo
      .text(v-html="project.preview_title")
      .button(@click="activateProject(index)", v-scroll-to="'#projects'") View details
</template>

<script>
export default {
  props: {
    activeSlide: {
      type: Number,
      default: 0,
    },
    projects: {
      type: Array,
      dafault() {
        return []
      },
    },
  },
  methods: {
    activateProject(index) {
      this.$emit('activateProject', index)
    },
  },
}
</script>
<style lang="sass" scoped>
  .slider
    .slide
      width: 487px
      height: 446px
      border: 5px solid #fff
      position: relative

      .art
        background: no-repeat center center
        background-size: contain
        position: absolute

      .logo
        position: absolute
        left: 44px
        top: 72px
        background: no-repeat center center
        background-size: contain

      .text
        color: #fff
        font-size: 40px
        font-weight: bold
        line-height: 46px
        letter-spacing: -0.6px
        text-align: left
        text-shadow: 0 1px 2px rgba(0, 0, 0, 0.39)
        position: absolute
        left: 44px
        top: 158px

      .button
        width: 189px
        height: 58px
        background-color: #ea564e
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.38)
        position: absolute
        left: 44px
        top: 322px
        text-transform: uppercase
        font-size: 13px
        font-weight: 500
        display: flex
        align-items: center
        justify-content: center
        color: #fff
        cursor: pointer

        &:hover
          background-color: darken(#ea564e, 5)

      &.l2p
        .logo
          width: 192px
          height: 41px
          background-image: url('../images/logo.svg')

      &.dota2
        .art
          width: 420px
          height: 575px
          background-image: url('../images/dota-hero.png')
          top: -90px
          left: 110px

        .logo
          width: 166px
          height: 33px
          background-image: url('../images/moremmr-logo.svg')


      &.lol
        .art
          width: 455px
          height: 621px
          background-image: url('../images/lol-hero.png')
          top: -15px
          left: 150px

        .logo
          width: 271px
          height: 54px
          left: 46px
          top: 47px
          background-image: url('../images/lol-logo.svg')

        .text
          top: 152px

      &.csgo
        .art
          width: 290px
          height: 496px
          background-image: url('../images/csgo-art.png')
          top: 96px
          left: 363px

        .logo
          width: 138px
          height: 60px
          left: 43px
          top: 50px
          background-image: url('../images/csgo-logo.png')

      &.overwatch
        .art
          width: 615px
          height: 528px
          background-image: url('../images/overwatch-hero.png')
          top: -41px
          left: 147px

        .logo
          width: 157px
          height: 101px
          left: 48px
          top: 17px
          background-image: url('../images/overwatch-logo.png')

      &.hs
        .art
          width: 255px
          height: 255px
          background-image: url('../images/hs-art.png')
          top: 155px
          left: 325px

        .logo
          width: 85px
          height: 85px
          left: 41px
          top: 43px
          background-image: url('../images/hs-logo.png')

      &.bng
        .art
          width: 338px
          height: 265px
          background-image: url('../images/cursor.png')
          top: 245px
          left: 225px

        .logo
          width: 196px
          height: 65px
          background-image: url('../images/bng-logo.svg')

      &.ebs
        .logo
          width: 128px
          height: 114px
          background-image: url('../images/ebs-logo.svg')
          top: 29px
          left: 51px

        .text
          top: 180px
          left: 47px
</style>



