<template lang="pug">
  .root
    .leftWrapper
      .left
    .rightWrapper
      .right
        .heading News
        .navigation
          .btn(@click="prev") <
          .text {{ currentItem + 1 }} / {{ news.length }}
          .btn(@click="next") >
        .newsContent(v-for="(item, index) in news", v-if="currentItem === index")
          .source {{ item.source }}
          .date {{ item.date }}
          .title {{ item.title }}
          .text(v-html="item.text")
          a.more(:href="item.url", target="_blank") more
</template>

<script>

export default {
  props: {
    news: {
      type: Array,
      dafault() {
        return []
      },
    },
  },
  data() {
    return {
      currentItem: 0,
    }
  },
  methods: {
    next() {
      if (this.currentItem !== this.news.length - 1) {
        this.currentItem += 1
      } else {
        this.currentItem = 0
      }
    },
    prev() {
      if (this.currentItem !== 0) {
        this.currentItem -= 1
      } else {
        this.currentItem = this.news.length - 1
      }
    },
  },
}
</script>

<style lang="sass" scoped>
.root
  width: 100%
  display: flex
  height: 100vh
  min-height: 600px
  min-width: 1200px
  overflow: hidden

  .leftWrapper
    display: flex
    flex: 1
    justify-content: flex-end
    background: no-repeat center center #292a2e url('./images/maks.jpg')
    background-size: cover

    .left
      width: 100%
      min-width: 600px
      max-width: 720px

  .rightWrapper
    display: flex
    flex: 1
    justify-content: flex-start
    padding-top: 60px
    background: no-repeat center center
    background-size: cover

    .right
      width: 100%
      min-width: 600px
      max-width: 720px
      padding: 0 10px 0 40px
      box-sizing: border-box
      position: relative

      .heading
        font-size: 34px
        font-weight: 900
        line-height: 69px
        letter-spacing: -0.7px
        text-align: left
        color: #000
        margin-bottom: 40px

      .navigation
        position: absolute
        top: 15px
        right: 0
        display: flex
        user-select: none

        .btn
          padding: 10px

          &:hover
            color: #ea564e
            cursor: pointer

        .text
          width: 50px
          padding: 10px
          text-align: center

      .newsContent
        font-size: 16px
        line-height: 29px
        letter-spacing: -0.3px
        text-align: left
        color: #000

        .source
          font-weight: 800
          text-transform: uppercase
          margin-bottom: 20px

        .date
          font-weight: 300
          font-size: 13px
          line-height: 21px
          color: #a2a2a2
          margin-bottom: 40px

        .title
          margin-bottom: 20px

        .text
          margin-bottom: 20px

        .more
          color: #ea564e
          display: inline-block

          &:hover
            text-decoration: underline
</style>



