<template lang="pug">
  .root
    .feedback
      .title(v-html="$options.title")
      a.btn(href="mailto:info@l2p.game") feedback
</template>

<script>
export default {
  title: 'We are always open for any suggestions<br/>and different variants of cooperation!',
}
</script>

<style lang="sass" scoped>
  .root
    .feedback
      min-width: 1200px
      height: 548px
      margin: 0 auto
      display: flex
      justify-content: center
      flex-direction: column
      align-items: center
      background: url('./images/bg.jpg') no-repeat
      background-size: cover

      .title
        margin-bottom: 50px
        font-size: 36px
        font-weight: 900
        line-height: 42px
        letter-spacing: -0.7px
        text-align: center
        color: #fff

      .btn
        width: 189px
        padding: 21px 0
        font-size: 13px
        font-weight: 500
        color: #fff
        cursor: pointer
        text-align: center
        text-transform: uppercase
        text-decoration: none
        background-color: #ea564e
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .38)
</style>



