<template lang="pug">
  .root
    .leftWrapper
      .left
        .header
          .logo
          .space
          // .lang
            | rus
            .chevron
          // .menu
            .button
        .title
          .white e-learning
          .red games
        .nav
          .item(
            v-for="(project, index) in projects",
            :class="{active: index === activeSlide}",
            @click="activeSlide = index; nextSlideTimer = sliderInterval"
          ) {{ project.nav_title }}
    .rightWrapper(:class="projects[activeSlide].id")
      .right
        main-slider(:activeSlide="activeSlide", :projects="projects", @activateProject="activateProject")
</template>

<script>
import MainSlider from './Slider'

export default {
  components: {
    MainSlider,
  },
  props: {
    projects: {
      type: Array,
      dafault() {
        return []
      },
    },
  },
  data() {
    return {
      activeSlide: 0,
      nextSlideTimer: 0,
      sliderInterval: 5,
    }
  },
  mounted() {
    this.nextSlideTimer = this.sliderInterval
    setInterval(this.nextSlide, 1000)
  },
  methods: {
    nextSlide() {
      if (this.nextSlideTimer === 0) {
        // eslint-disable-next-line
        (this.activeSlide !== this.projects.length - 1) ? this.activeSlide += 1 : this.activeSlide = 0
        this.nextSlideTimer = this.sliderInterval
      } else {
        this.nextSlideTimer -= 1
      }
    },
    activateProject(index) {
      this.$emit('activateProject', index)
    },
  },
}
</script>

<style lang="sass" scoped>
.root
  width: 100%
  display: flex
  height: 100vh
  min-height: 720px
  min-width: 1200px
  overflow: hidden

  .leftWrapper
    display: flex
    flex: 1
    justify-content: flex-end
    background-color: #292a2e

    .left
      width: 100%
      min-width: 600px
      max-width: 720px
      padding: 0 40px 0 10px
      box-sizing: border-box

      .header
        padding-top: 40px
        width: 100%
        height: 41px
        display: flex
        margin-bottom: 100px

        .logo
          flex: 0 0 192px
          height: 41px
          background: no-repeat center center url('../images/logo.svg')

        .space
          flex: 1

        .lang
          display: flex
          align-items: center
          flex: 0 0 40px
          color: #fff
          font-size: 11px
          font-weight: 500
          text-transform: uppercase
          margin-right: 40px

          .chevron
            margin-left: 10px
            width: 10px
            height: 6px
            background: no-repeat center center url('../images/chevron.svg')
            position: relative
            top: -2px


        .menu
          display: flex
          align-items: center
          flex: 0 0 19px

          .button
            width: 19px
            height: 17px
            background: no-repeat center center url('../images/menu.svg')


      .title
        text-transform: uppercase
        padding-bottom: 40px
        margin-right: 45px
        border-bottom: 1px solid rgba(#979797,.2)
        margin-bottom: 60px

        .white
          color: #fff
          font-size: 46px
          font-weight: 300
          letter-spacing: -1px
          text-align: left

        .red
          font-size: 92px
          font-weight: 900
          line-height: 0.9
          color: #ea564e

      .nav
        margin-right: 45px

        .item
          color: #fff
          font-size: 15px
          font-weight: 500
          line-height: 20px
          text-align: left
          padding: 10px

          &:hover
            background: rgba(#000, .2)
            cursor: pointer

          &.active
            color: #ea564e

  .rightWrapper
    display: flex
    flex: 1
    justify-content: flex-start
    background: no-repeat center center
    background-size: cover

    &.bng
      background-image: url('../images/bng-background.jpg')

    &.l2p
      background-image: url('../images/bng-background.jpg')

    &.dota2
      background-image: url('../images/dota-background.jpg')

    &.lol
      background-image: url('../images/lol-background.jpg')

    &.csgo
      background-image: url('../images/csgo-background.jpg')

    &.overwatch
      background-image: url('../images/overwatch-background.jpg')

    &.hs
      background-image: url('../images/hs-background.jpg')

    &.ebs
      background-image: url('../images/ebs-background.jpg')

    .right
      width: 100%
      min-width: 600px
      max-width: 720px
      display: flex
      justify-content: center
      align-items: center
</style>



