<template lang="pug">
  .features
    .items
      .feature(v-for="(item, index) in $options.features", :key="index")
        .number {{ item.number }}
        .label {{ item.label }}
</template>

<script>
export default {
  features: [{
    number: '26.67m$',
    label: 'Investments',
  }, {
    number: '449,006',
    label: 'Engagements',
  }, {
    number: '49,079',
    label: 'Played games',
  }, {
    number: '330',
    label: 'teams',
  }],
}
</script>

<style lang="sass" scoped>
  .features
    min-width: 1200px
    max-width: 1440px
    margin: 0 auto

    .items
      padding: 70px
      display: flex
      justify-content: space-between
      align-items: center

      .feature
        text-align: center

        .number
          font-size: 34px
          font-weight: 900
          line-height: 68px
          letter-spacing: -0.7px
          text-transform: uppercase
          color: #d96156

        .label
          font-size: 16px
          font-weight: 900
          line-height: 32px
          letter-spacing: -0.3px
</style>



