<template lang="pug">
  .root#projects
    .project(v-for="(project, index) in projects", :class="[project.id, 'fade-in-1']", v-if="activeProject === index")
      .leftWrapper
        .left
          .logo
          .title {{ project.title }}
          .text(v-html="project.text")
          .buttons
            a(:href="project.site", target="_blank").site Site
            .next(@click="nextProject") Next project
      .rightWrapper
        .right
</template>

<script>
export default {
  props: {
    projects: {
      type: Array,
      dafault() {
        return []
      },
    },
  },
  data() {
    return {
      activeProject: 0,
    }
  },
  methods: {
    nextProject() {
      if (this.activeProject !== this.projects.length - 1) {
        this.activeProject += 1
      } else {
        this.activeProject = 0
      }
    },
  },
}
</script>

<style lang="sass" scoped>
  .root
    width: 100%
    display: flex
    height: 100vh
    min-height: 720px
    min-width: 1200px
    background-color: #292a2e

    .project
      width: 100%
      display: flex
      background: no-repeat center center
      background-size: cover

      .leftWrapper
        display: flex
        flex: 1
        justify-content: flex-end

        .left
          width: 100%
          min-width: 600px
          max-width: 720px
          padding: 0 40px 0 10px
          box-sizing: border-box
          display: flex
          flex-direction: column
          justify-content: center

          .logo
            background: no-repeat center center
            background-size: contain
            margin-bottom: 70px

          .title
            color: #fff
            font-size: 40px
            font-weight: bold
            line-height: 46px
            letter-spacing: -0.6px
            text-align: left
            text-shadow: 0 1px 2px rgba(#000, 0.39)
            margin-bottom: 40px

          .text
            margin-bottom: 60px
            color: #fff
            font-size: 16px
            line-height: 29px
            letter-spacing: -0.3px
            text-align: left

            /deep/ a
              color: #ea564e
              text-decoration: none

              &:hover
                text-decoration: underline

          .buttons
            display: flex
            margin-bottom: 100px

            .site
              width: 189px
              height: 58px
              background-color: #ea564e
              box-shadow: 0 2px 4px 0 rgba(#000, 0.38)
              text-transform: uppercase
              font-size: 13px
              font-weight: 500
              display: flex
              align-items: center
              justify-content: center
              color: #fff
              cursor: pointer
              margin-right: 40px
              text-decoration: none

              &:hover
                background-color: darken(#ea564e, 5)

            .next
              padding: 0 35px 0 20px
              height: 58px
              text-transform: uppercase
              font-size: 13px
              font-weight: 500
              display: flex
              align-items: center
              justify-content: center
              color: #fff
              cursor: pointer
              position: relative

              &:after
                content: ''
                width: 13px
                height: 13px
                background: no-repeat center center url('../images/chevron.svg')
                background-size: contain
                position: absolute
                top: 21px
                right: 10px
                transform: rotate(270deg)

              &:hover
                background-color: rgba(#000, .2)
                box-shadow: 0 2px 4px 0 rgba(#000, 0.38)

      .rightWrapper
        display: flex
        flex: 1
        align-items: center

        .right
          display: flex
          flex: 1
          min-width: 600px
          max-width: 720px
          height: 100%
          background: no-repeat center center
          background-size: contain

      &.l2p
        background: #fff

        .leftWrapper
          .left
            .logo
              display: none

            .title, .text
              color: #000

            .buttons
              .site
                display: none

              .next
                color: #000

                &:hover
                  background-color: rgba(#000, .05)

        .rightWrapper
          background: no-repeat center center url('./images/l2p-preview.jpg')
          background-size: cover

      &.dota2
        background-image: url('./images/dota-bg.jpg')

        .leftWrapper
          .left
            .logo
              width: 166px
              height: 33px
              background-image: url('../images/moremmr-logo.svg')

        .rightWrapper
          .right
            background-image: url('./images/dota-preview.png')

      &.lol
        background-image: url('./images/lol-bg.jpg')

        .leftWrapper
          .left
            .logo
              width: 271px
              height: 54px
              background-image: url('../images/lol-logo.svg')

        .rightWrapper
          .right
            background-image: url('./images/lol-preview.png')

      &.csgo
        background-image: url('./images/csgo-bg.jpg')

        .leftWrapper
          .left
            .logo
              width: 138px
              height: 60px
              background-image: url('../images/csgo-logo.png')

        .rightWrapper
          .right
            background-image: url('./images/csgo-preview.png')

      &.overwatch
        background-image: url('./images/overwatch-bg.jpg')

        .leftWrapper
          .left
            .logo
              width: 157px
              height: 101px
              background-image: url('../images/overwatch-logo.png')

        .rightWrapper
          .right
            background-image: url('./images/overwatch-preview.png')

      &.hs
        background-image: url('./images/hs-bg.jpg')

        .leftWrapper
          .left
            .logo
              width: 85px
              height: 85px
              background-image: url('../images/hs-logo.png')

        .rightWrapper
          .right
            background-image: url('./images/hs-preview.png')

      &.bng
        background-image: url('./images/bng-bg.jpg')

        .leftWrapper
          .left
            .logo
              width: 196px
              height: 65px
              background-image: url('../images/bng-logo.svg')

        .rightWrapper
          .right
            background-image: url('./images/bng-preview.png')

      &.ebs
        background-image: url('./images/ebs-bg.jpg')

        .leftWrapper
          .left
            .logo
              width: 128px
              height: 114px
              background-image: url('../images/ebs-logo.svg')

        .rightWrapper
          .right
            background-image: url('./images/ebs-preview.png')
</style>



