<template lang="pug">
  .about
    .block
      .info
        .title {{ $options.title }}
        .description {{ $options.description }}
        ul.list
          li.item(v-for="(item, index) in $options.list", :key="index") {{ item }}
    .block
      .image
</template>

<script>
export default {
  title: 'L2P Limited',
  description: 'Our mission is of teaching and full media-support of beginner players as well as skilled ones and teams, of new talents developing and drawing attention of world brands to E-Sport sphere by means of creation of the united supportive infrastructure for all its participants. We strive to develop the Industry and to evolve along with it in many areas:',
  list: [
    'E-Learning on the most popular games',
    'Creation and guiding of new e-sports teams',
    'Arrangement of gaming related competitions and events',
    'Advertising and brand promotion in the E-Sport audience',
    'Complete legal and media assistance for professional players and beginners alike',
  ],
}
</script>

<style lang="sass" scoped>
  .about
    min-width: 1200px
    max-width: 1440px
    min-height: 613px
    margin: 0 auto
    display: flex

    .block
      &:nth-child(1)
        flex-basis: 50%
      &:nth-child(2)
        flex-basis: 50%

      .info
        padding: 30px 0 30px 15px
        margin-right: 40px

        .title
          margin-bottom: 20px
          font-size: 34px
          font-weight: 900
          line-height: 68px
          letter-spacing: -0.7px

        .description
          font-size: 16px
          line-height: 30px
          letter-spacing: -0.3px

        .list
          margin-top: 40px
          padding: 0
          list-style-type: none

          .item
            padding-left: 30px
            vertical-align: middle
            font-size: 16px
            font-weight: 500
            line-height: 32px
            letter-spacing: -0.3px
            background: url('./images/apply.svg') no-repeat 0 4px

            &:not(:last-child)
              margin-bottom: 20px

      .image
        width: 100%
        height: 100%
        background: url('./images/bg.jpg') no-repeat center
        background-size: cover
</style>



